import * as styleConstants from  '../constants/StyleConstants';
import { Parallax } from 'react-scroll-parallax';
export default ({videoSrc,index}) => {
    return <Parallax 
                y={[-35, 35]}
                offsetYMax={150}
                offsetYMin={-120}>
        <style jsx>{`
            .bigvid {
                width:100%;
                z-index:${index};
                display:block;
                position:relative;
                ${index != 0 ? "transform: translateY(40%)" : ""}
            }
        `}</style>
        <video autoPlay={true} muted={true} loop={true} className="bigvid">
            <source src={videoSrc} type="video/mp4" />
        </video>
    </Parallax>
}